<template>
  <div class="manage-custom-component-page">
    <div class="title">自定义组件管理
      <div class="manage-custom-component-tool">
        <Button type="default"
                shape="circle"
                icon="md-add"
                @click="addCustomComponent">添加自定义组件</Button>
      </div>
    </div>

    <Layout>
      <Sider hide-trigger
             :width="247"
             :style="'height: ' + areaHeight + 'px; overflow-y: auto;'">
        <Menu :theme="'light'"
              @on-select="selectTeam"
              v-if="companies && companies[0]"
              :active-name="companies[0].id"
              :open-names="['company', 'org', 'project']">
          <Submenu name="company">
            <template slot="title">
              <Icon type="ios-people" />
              当前公司
            </template>
            <MenuItem v-if="companies && companies[0]"
                      :name="companies[0].id">{{ companies[0].name }}</MenuItem>
          </Submenu>
          <Submenu name="org">
            <template slot="title">
              <Icon type="md-people" />
              团队
            </template>
            <MenuItem v-for="(item, index) in orgs"
                      :key="index"
                      :name="'2-' + item.id">{{ item.name }}</MenuItem>
          </Submenu>
          <Submenu name="project">
            <template slot="title">
              <Icon type="md-clipboard" />
              项目
            </template>
            <MenuItem v-for="(item, index) in project"
                      :key="index"
                      :name="'3-' + item.id">{{ item.name }}</MenuItem>
          </Submenu>
        </Menu>
      </Sider>
      <Layout>
        <Content :style="'height: ' + areaHeight + 'px; overflow-y: auto;'">
          <div class="manage-custom-component-table">
            <Row class="manage-custom-component-title"
                 :gutter="16">
              <Col :span="rowColsMap.index">序号</Col>
              <Col :span="rowColsMap.code">Code</Col>
              <Col :span="rowColsMap.name">名称</Col>
              <Col :span="rowColsMap.description">描述</Col>
              <Col :span="rowColsMap.inputType">输入方式</Col>
              <Col :span="rowColsMap.menu">操作</Col>
            </Row>
            <div class="manage-custom-component-scoll">
              <Row class="manage-custom-component-item"
                   v-for="(item, index) in componentListCustom"
                   :key="index"
                   :gutter="10">
                <Col :span="rowColsMap.index"
                     class="cindex">{{ index + 1 }}</Col>
                <Col :span="rowColsMap.code"
                     class="cindex">{{ item.code || '&nbsp;' }}</Col>
                <Col :span="rowColsMap.name"
                     style="font-weight: 600">
                <span @click="editCustomComponent(item, index)"
                      style="cursor: pointer">{{ item.name || '&nbsp;' }}</span>
                </Col>
                <Col :span="rowColsMap.description">{{ item.remarks || '&nbsp;' }}</Col>
                <Col :span="rowColsMap.inputType">
                <span class="component-name-border">{{ item.inputType }}</span>
                </Col>
                <Col :span="rowColsMap.menu">
                <Button type="primary"
                        @click="editCustomComponent(item, index)"
                        style="margin-right: 10px">配置</Button>
                <Button type="primary"
                        v-show="item.inputType === '树形'"
                        @click="dataCustomComponent(item, index)"
                        style="margin-right: 10px">数据</Button>
                <Poptip confirm
                        placement="bottom"
                        style="text-align: left"
                        title="您确认删除这个自定义组件吗？"
                        @on-ok="deleteCustomComponent(item, index)">
                  <Button type="error">删除</Button>
                </Poptip>
                </Col>
              </Row>
            </div>
          </div>
        </Content>
      </Layout>
    </Layout>

    <customComponentEdit v-model="showCustomComponent"
                         :editItem="editItem"
                         :judgeComponent="judgeComponent"
                         :org="filterId[0]"
                         :team="filterId[1]"
                         :teamList="orgs"
                         :projectList="project"
                         @updateList="updateCustomComponentList"></customComponentEdit>
  </div>
</template>

<script>
import { getCompontentCache, clearCompontentCache, delCompontent, getSimpleCustomList } from '@/api/component'
import org from '@/api/org'
import project from '@/api/project'
import customComponentEdit from '@/components/custom-component/edit'

export default {
  name: 'manage-custom-component-page',
  components: {
    customComponentEdit
  },
  computed: {
    componentList () {
      return this.$store.state.component.list
    },
    componentQuestion () {
      return this.$store.state.component.question
    },
    componentOptions () {
      return this.$store.state.component.options
    },
    companyList () {
      return this.$store.state.user.companyList
    },
    currentCompany () {
      return this.$store.state.user.currentCompany
    }
  },
  data () {
    return {
      showCustomComponent: false,
      editItem: null,
      rowColsMap: {
        index: 2,
        code: 2,
        name: 4,
        description: 5,
        inputType: 4,
        menu: 7
      },
      dataItem: null,
      companies: [],
      orgs: [],
      filterId: [null, null],
      componentListCustom: [],
      relationComponentAOList: [],
      judgeComponent: {},
      project: [],
      areaHeight: window.innerHeight - 300
    }
  },
  async created () {
    // org.getTeamList({
    //   rangeType: 1
    // }),
    const r = await Promise.all([
      // getLoginCompanies(), // 获取当前用户的公司
      org.getTeamList({ // 获取当前用户所属团队
        rangeType: 2
      }),
      project.getProjectList({
        orgId: '',
        rangeType: 1,
        type: 1
      })
    ])
    // console.log(r)
    this.companies = this.companyList
    this.filterId[0] = this.currentCompany
    this.orgs = r[0]
    this.project = r[1]
    await this.updateCustomComponentList()
  },
  methods: {
    selectTeam (id) {
      if (this.filterId[0] === id) {
        this.filterId[1] = null
      } else {
        this.filterId[1] = id
      }
      this.updateCustomComponentList()
    },
    async editCustomComponent (item) {
      clearCompontentCache()
      this.editItem = await getCompontentCache(item.id) // 获取当前选择的组件的全部信息
      this.showCustomComponent = true
    },
    dataCustomComponent (item) {
      this.dataItem = item
      this.$router.push({
        name: 'DataComponent',
        query: {
          id: item.id
        }
      })
    },
    async updateCustomComponentList () {
      const orgFilter = {
        sourceId: this.filterId[0],
        sourceType: 1
      }

      const relationComponentAOList = []
      if (this.filterId[1]) {
        const teamFilter = {
          sourceId: this.filterId[1].split('-')[1],
          sourceType: parseInt(this.filterId[1].split('-')[0])
        }

        relationComponentAOList.push(teamFilter)
      } else {
        relationComponentAOList.push(orgFilter)
      }

      this.componentListCustom = await getSimpleCustomList({
        relationComponentAOList
      }) // 获取当前公司的自定义组件列表
    },
    async addCustomComponent () {
      this.editItem = null
      clearCompontentCache()
      this.judgeComponent = await getCompontentCache(this.componentQuestion['judge'].id)
      this.showCustomComponent = true
    },
    async deleteCustomComponent (item) {
      if (await delCompontent(item.id)) {
        this.$Message.success('删除成功')
        this.updateCustomComponentList()
      } else {
        this.$Modal.error({
          title: '错误',
          content: '<p>因自定义组件正在被使用，删除该自定义组件失败</p>',
          loading: true,
          cancelText: '',
          onOk: () => {
            setTimeout(() => {
              this.$Modal.remove()
            }, 1000)
          }
        })
      }
    }
    // componentName (item) { // 后端返回了inputType
    //   for (const n in this.componentList) {
    //     if (this.componentList[n].id === item.nestComponentId) {
    //       return this.componentList[n].name
    //     }
    //   }
    // }
  }
}
</script>

