<template>
  <div class="org-info">
    <div class="org-list">
      <h2 class="org-title">公司</h2>
      <ul>
        <li v-for="(company, index) in companyList" :key="index">
          <Button class="org-item">
            <span class="org-edit" @click.stop="editCompanyInfo(company)">
              <Icon type="md-create"></Icon>
            </span>
            <span class="org-name">
              {{company.name}}
            </span>
          </Button>
        </li>
      </ul>
    </div>
    <div class="org-list">
      <h2 class="org-title">团队列表</h2>
      <ul>
        <li v-for="(team, index) in teamList" :key="index">
          <Button class="org-item">
            <span class="org-edit" @click.stop="editTeamInfo(team)">
              <Icon type="md-create"></Icon>
            </span>
            <span class="org-name">
              {{team.name}}
            </span>
            <span class="org-delete" @click.stop="removeOrg(team.id)">
              <Icon type="md-close"></Icon>
            </span>
          </Button>
        </li>
        <li>
          <Button class="org-add" @click="addTeam()">
            <Icon type="md-add"></Icon>
          </Button>
        </li>
      </ul>
    </div>
    <Modal
      class-name="vertical-center-modal"
      v-model="teamEdit"
      ok-text="保存"
      cancel-text="取消"
      @on-ok="saveTeamEdit()"
      >
      <div class="org-edit">
        <div class="edit-line">
          <span class="edit-title">名称</span>
          <Input v-model="teamData.name" />
        </div>
        <div class="edit-line">
          <div class="eidt-title">成员</div>
          <Button v-for="(user, index) in memberList" :key="index" class="edit-user" :type="userChange.delUserIds.indexOf(user.id)!==-1 ? '':'primary'">
            {{user.name}}
            <span class="delete-user" @click.stop="cancelRemoveUser(user.id)" v-if="userChange.delUserIds.indexOf(user.id)!==-1">
              <Icon type="md-add"></Icon>
            </span>
            <span class="delete-user" @click.stop="removeOrgUser(user.id, 1)" v-else>
              <Icon type="md-close"></Icon>
            </span>
          </Button>
          <Button v-for="(user, index) in userChange.addUserIds" :key="index" class="edit-user" type="success">
            {{user.name}}
            <span class="delete-user" @click.stop="removeOrgUser(user.id, 2)">
              <Icon type="md-close"></Icon>
            </span>
          </Button>
          <div class="add-user">
            <Select
              v-model="userAdd"
              filterable
              remote
              multiple
              :remote-method="generateReadyList"
              :loading="loading">
              <Option v-for="(user, index) in readyList" :value="user.id" :key="index">{{user.name}}</Option>
            </Select>
            <Button @click="addUserToOrg()">
              <Icon type="md-add"></Icon>
            </Button>
          </div>
        </div>
      </div>
    </Modal>
    <Modal
      class-name="vertical-center-modal"
      v-model="companyEdit"
      ok-text="保存"
      cancel-text="取消"
      @on-ok="saveCompanyEdit()"
      >
      <div class="edit-line">
        <h2>{{companyData.name}}</h2>
      </div>
      <div class="org-edit">
        <div class="edit-line">
          <div class="eidt-title">成员</div>
          <Button v-for="(user, index) in memberList" :key="index" class="edit-user" :type="userChange.delUserIds.indexOf(user.id)!==-1 ? '':'primary'">
            {{user.name}}
            <span class="delete-user" @click.stop="cancelRemoveUser(user.id)" v-if="userChange.delUserIds.indexOf(user.id)!==-1">
              <Icon type="md-add"></Icon>
            </span>
            <span class="delete-user" @click.stop="removeOrgUser(user.id, 1)" v-else>
              <Icon type="md-close"></Icon>
            </span>
          </Button>
          <Button v-for="(user, index) in userChange.addUserIds" :key="index" class="edit-user" type="success">
            {{user.name}}
            <span class="delete-user" @click.stop="removeOrgUser(user.id, 2)">
              <Icon type="md-close"></Icon>
            </span>
          </Button>
          <div class="add-user">
            <Select
              v-model="userAdd"
              filterable
              remote
              multiple
              :remote-method="generateReadyList"
              :loading="loading">
              <Option v-for="(user, index) in readyList" :value="user.id" :key="index">{{user.name}}</Option>
            </Select>
            <Button @click="addUserToOrg()">
              <Icon type="md-add"></Icon>
            </Button>
          </div>
        </div>
      </div>
    </Modal>
  </div>
</template>

<script>
import org from '@/api/org'

export default {
  name: 'org-board',
  data () {
    return {
      teamEdit: false,
      companyEdit: false,
      teamList: [],
      teamData: {
        id: 0,
        name: ''
      },
      companyData: {
        id: 0,
        name: ''
      },
      memberList: [], // 用来暂存成员列表
      userList: [], // 用来暂存用户列表
      readyList: [], // 输入用户名时备选用户列表
      userAdd: [],
      loading: false,
      userChange: {
        orgId: 0,
        addUserIds: [],
        delUserIds: []
      }
    }
  },
  methods: {
    async getTeamList () {
      let data = await org.getTeamList({})
      if (data) {
        this.teamList = data.result
      }
    },
    async addTeam () {
      let data = await org.addTeam({ 'createUserId': 201, name: '未命名' })
      if (data) {
        this.teamList.push(data)
      }
    },
    async removeOrg (id) {
      let res = await org.removeOrg(id)
      if (res) {
        this.getTeamList()
      }
    },
    async editTeamInfo (team) {
      this.teamEdit = true
      this.userChange = {
        orgId: team.id,
        addUserIds: [],
        delUserIds: []
      }
      this.teamData = {
        id: team.id,
        name: team.name
      }
      let res = await org.editTeamInfo()
      if (res) {
        this.memberList = res.data.data.userAOList
      }
      res = await org.editTeamInfo1({})
      if (res) {
        this.userList = res.data.data.result
      }
      console.log('editteaminfo')
    },
    generateReadyList (query) {
      if (query !== '') {
        this.loading = true
        setTimeout(() => {
          this.loading = false
          this.readyList = this.userList.filter(user => user.name.indexOf(query) > -1)
        }, 200)
      } else {
        this.readyList = []
      }
    },
    removeOrgUser (id, type) {
      if (type === 1) {
        this.userChange.delUserIds.push(id)
      } else if (type === 2) {
        let idx = -1
        for (let user of this.userAdd) {
          idx++
          if (user.id === id) break
        }
        if (idx >= -1) {
          this.userChange.addUserIds.splice(idx, 1)
        }
      }
    },
    cancelRemoveUser (id) {
      let idx = this.userChange.delUserIds.indexOf(id)
      if (idx > -1) {
        this.userChange.delUserIds.splice(idx, 1)
      }
    },
    addUserToOrg () {
      for (let user of this.userList) {
        if (this.userAdd.indexOf(user.id) > -1) {
          let list = this.userChange.addUserIds.filter(oldUser => oldUser.id === user.id)
          let list2 = this.memberList.filter(mem => mem.id === user.id)
          if (list.length === 0 && list2.length === 0) {
            this.userChange.addUserIds.push(user)
          }
        }
      }
      this.userAdd = []
    },
    async saveTeamEdit () {
      let res = await org.saveTeamEdit(this.teamData)
      if (res) {
        this.getTeamList()
        this.$Message.success('保存成功')
      } else {
        this.$Message.error('保存失败')
      }
      if (this.userChange.addUserIds.length !== 0 || this.userChange.delUserIds.length !== 0) {
        let memInfo = {
          orgId: this.userChange.orgId,
          addUserIds: [],
          delUserIds: []
        }
        this.userChange.addUserIds.map(item => {
          memInfo.addUserIds.push(item.id)
        })
        this.userChange.delUserIds.map(item => {
          memInfo.delUserIds.push(item.id)
        })
        res = await org.saveTeamEdit1(memInfo)
        if (res) {
          this.$Message.success('保存成功')
        }
      }
    },
    async editCompanyInfo (company) {
      this.companyEdit = true
      this.userChange = {
        orgId: company.id,
        addUserIds: [],
        delUserIds: []
      }
      this.companyData = {
        id: company.id,
        name: company.name
      }
      let res = await Request.put({ companyId: company.id })
      if (res.status === 200 && res.data.response.code === 0) {
        this.memberList = res.data.data
      }
      console.log(res, 'editCompanyInfo', company.id)
      res = await org.editCompanyInfo1({})
      if (res.status === 200 && res.data.response.code === 0) {
        this.userList = res.data.data.result
      }
    },
    async saveCompanyEdit () {
      if (this.userChange.addUserIds.length !== 0 || this.userChange.delUserIds.length !== 0) {
        let memInfo = {
          orgId: this.userChange.orgId,
          addUserIds: [],
          delUserIds: []
        }
        this.userChange.addUserIds.map(item => {
          memInfo.addUserIds.push(item.id)
        })
        this.userChange.delUserIds.map(item => {
          memInfo.delUserIds.push(item.id)
        })
        let res = await org.saveCompanyEdit(memInfo)
        if (res.status === 200 && res.data.response.code === 0) {
          this.$Message.success('保存成功')
        }
      }
    }
  },
  computed: {
    companyList () {
      return this.$store.state.user.companyList
    }
  },
  mounted () {
    this.getTeamList()
    setInterval(() => {
      // console.log(this.userAdd)
    }, 2000)
  },
  watch: {
    teamEdit (oldValue, newValue) {
      if (!newValue) {
        this.getTeamList()
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.org-info
  width 15%
  height 100%
  display flex
  flex-direction column
  align-items center
  .org-list
    border 1spx solid red
    margin 1rem auto
    display flex
    flex-direction column
    width 8rem
    li
      width 100%
      margin 0.1rem
      .org-item
        width 100%
        text-align left
      .org-add
        width 100%
.org-delete
  display none
.org-item:hover .org-delete
  display inline
  float right
.add-user
  display flex
.edit-line
  padding 0.5rem
  .edit-user
    margin 0.5rem
  .delete-user
    display none
  .edit-user:hover .delete-user
    display inline
</style>
