<template>
  <div class="project-page">
    <div class="title">项目</div>
    <div class="page-content">
      <div class="menu-list">
        <div
          :class="{
            'menu-item': true,
            'choose-menu': menuSelected.rangeType === constMap.personal
          }"
          @click.stop="selectMenu(1)">
          我的项目
        </div>
        <div
          :class="{
            'menu-item': true,
            'choose-menu': menuSelected.rangeType === constMap.company
          }"
          @click.stop="selectMenu(2)">
          公司项目
        </div>
        <div class="menu-items">
          <div class="menu-item-title">
            <div class="devide-line"></div>
            <div class="title-text" v-if="teamList.length">
              <span>团队项目</span>
            </div>
            <div class="devide-line"></div>
          </div>
          <div
            :class="{
              'menu-team-item': true,
              'choose-menu': menuSelected.orgId === team.id
            }"
            v-for="team of teamList"
            :key="team.id"
            @click.stop="selectMenu(3, team.id)"
            v-if="team.companyId">
            <Icon title="公司团队" class="team-icon-company" type="md-people"></Icon>
            <span class="team-name">{{ team.name }}</span>
          </div>
          <div
            :class="{
              'menu-team-item': true,
              'choose-menu': menuSelected.orgId === team.id
            }"
            v-for="team of teamList"
            :key="team.id"
            @click.stop="selectMenu(3, team.id)"
            v-if="!team.companyId">
            <Icon title="个人团队" class="team-icon-personal" type="md-people"></Icon>
            <span class="team-name">{{ team.name }}</span>
          </div>
        </div>
      </div>
      <div class="project-list">
        <Button
          @click.stop="tryAddProject"
          icon="md-add"
          class="project-add">
        </Button>
        <div
          @click.stop="openProject(project.id)"
          @mouseleave="hideProjectMenu"
          :class="{
            project: true,
            'detail-project': projectDetail.project === project
          }"
          v-for="(project, index) in projectList" :key="index">
          <div class="project-menu">
            <div class="project-menu-button">
              <span
                @click.stop
                @mouseenter="showProjectMenu(project)"
                class="project-menu-icon">
                <Icon type="md-menu" class="menu-icon"></Icon>
              </span>
            </div>
            <div
              class="project-menu-items"
              @mouseleave="hideProjectMenu"
              v-show="projectMenu.showId === project.id">
              <div
                class="project-menu-item"
                v-for="(item, idx) of projectMenu.menuItems"
                @click.stop="clickProjectMenu(item, project)"
                :key="idx">
                {{ item.title }}
              </div>
            </div>
          </div>
          <div class="project-icon">
            <div class="project-abstract" v-for="(idx, index) in 7" :key="index"></div>
          </div>
          <div class="project-name" :title="project.name">
            {{ project.name }}
          </div>
        </div>
      </div>
      <div class="project-detail" v-show="projectDetail.show">
        <div class="detail-menu">
          <Icon class="detail-menu-icon" @click.stop="hideProjectDetail" type="md-close"></Icon>
        </div>
        <div class="detail-item base-info">
          <div class="detail-title">
            <span class="detail-title-content">基本信息</span>
            <div class="detail-title-line"></div>
          </div>
          <div class="base-info-content">
            <div class="base-info-item">
              <div class="info-item-title">项目名称</div>
              <Input
                @on-change="changeProjectInfo"
                @on-blur="saveProjectInfo"
                @on-enter="saveProjectInfo"
                v-model="projectDetail.project.name"></Input>
            </div>
            <div class="base-info-item">
              <div class="info-item-title">显示名称</div>
              <Input
                @on-change="changeProjectInfo"
                @on-blur="saveProjectInfo"
                @on-enter="saveProjectInfo"
                v-model="projectDetail.project.showName"></Input>
            </div>
            <div class="base-info-item">
              <div class="info-item-title">英文名称</div>
              <Input
                @on-change="changeProjectInfo"
                @on-blur="saveProjectInfo"
                @on-enter="saveProjectInfo"
                v-model="projectDetail.project.enName"></Input>
            </div>
            <div class="base-info-item">
              <div class="info-item-title">项目描述</div>
              <Input
                :autosize="{
                  minRows: 5
                }"
                @on-change="changeProjectInfo"
                @on-keyup.ctrl.enter="saveProjectInfo"
                @on-blur="saveProjectInfo"
                v-model="projectDetail.project.remarks"
                type="textarea"></Input>
            </div>
          </div>
        </div>
        <div class="detail-item detail-company" v-if="projectDetail.project.companyId || userInfo.userType === constMap.manager">
          <div class="detail-title">
            <span class="detaile-title-content">公司</span>
            <div class="detail-title-line"></div>
          </div>
          <div class="detail-company-content">
            <template v-if="projectDetail.project.companyId">
              <div class="company-name">
                {{ projectDetail.project.companyName }}
              </div>
            </template>
            <template v-else>
              <Button type="default" @click="setToCompany">移动到当前公司</Button>
              <div class="notice">注意: 个人项目移动到公司为不可逆操作，请谨慎。目前暂不支持公司项目转让。</div>
            </template>
          </div>
        </div>
        <div class="detail-item detail-team">
          <div class="detail-title">
            <span class="detail-title-content">团队</span>
            <div class="detail-title-line"></div>
          </div>
          <div class="detail-team-content">
            <div class="team-abstract" v-if="projectDetail.project.orgId">
              <span class="team-icon">
                <Icon type="md-people"></Icon>
              </span>
              <span class="team-name">
                {{ projectDetail.project.orgName }}
              </span>
            </div>
            <Button
              @click.stop="setTeam"
              class="detail-team-set">
              <span v-if="projectDetail.project.orgId">更改</span>
              <span v-else>设置</span>
            </Button>
          </div>
        </div>
        <div class="detail-item detail-members">
          <div class="detail-title">
            <span class="detail-title-content">成员</span>
            <div class="detail-title-line"></div>
          </div>
          <div class="detail-members-content">
            <div class="detail-member add-member">
              <div
                @click.stop="addProjectMember()"
                class="add-member-button">
                <Icon type="md-add"></Icon>
              </div>
            </div>
            <div
              class="detail-member"
              v-for="member of projectDetail.memberList"
              :key="member.id">
              <div class="avatar" :title="member.name">
                <img
                  :src="member.avatar ? $avatarUrl.replace('avatarID', member.avatar) : '@/assets/logo.png'"
                  class="avatar-img"
                  alt="用户头像">
                <div class="avatar-name">{{ member.name }}</div>
              </div>
              <div class="user-info">
                <div class="info-item" v-if="member.userType === constMap.manager">
                  <Icon type="md-person" class="info-icon"></Icon>
                  <span class="info-content">管理员</span>
                </div>
              </div>
              <div class="member-menu">
                <Select
                  v-if="(userInfo.userType !== constMap.member || roleInfo.userType !== constMap.member) && (userInfo.id !== member.id)"
                  :transfer="true"
                  v-model="member.userType"
                  @on-change="changePermission(member)"
                  style="width:6rem">
                  <Option v-for="(option, index) in userTypeList" :value="option.code" :key="index">
                    {{option.name}}
                  </Option>
                </Select>
                <Button type="default" @click.stop="tryRemoveProjectmember(member)">
                  <span v-if="userInfo.id === member.id">退出</span>
                  <span v-if="(userInfo.userType !== constMap.member || roleInfo.userType !== constMap.member) && (userInfo.id !== member.id)">删除</span>
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Modal
      title="请输入项目标题"
      v-model="newProject.show">
      <div class="new-project">
        <div class="new-project-input">
          <Input
            ref="newProjectInput"
            v-model="newProject.info.name"
            @on-enter="confirmAddProject"
            placeholder="请输入项目标题"></Input>
        </div>
        <div class="check-enter">
          <Checkbox v-model="newProject.enter">创建后进入项目编辑页面</Checkbox>
        </div>
      </div>
      <div slot="footer">
        <Button type="text" @click.stop="clearNewProjectInfo">取消</Button>
        <Button type="primary" @click.stop="confirmAddProject">确定</Button>
      </div>
    </Modal>
    <Modal
      title="设置项目团队"
      v-model="projectDetail.teamEditShow"
      :mask-closable="false"
      :width="400"
      :closable="false"
      :transfer="true">
      <div class="project-team-set">
        <Row class="project-team-choose" type="flex" align="middle" justify="center">
          <Col :span="6">
            <span>
            请选择团队
            </span>
          </Col>
          <Col :span="6">
            <Select
              @on-change="getTeamMembers"
              v-model="projectDetail.teamEditInfo.orgId">
              <Option
                v-for="(option, index) of projectDetail.teamEditInfo.readyTeam"
                :value="option.id"
                :label="option.name"
                :key="index">
              </Option>
            </Select>
          </Col>
        </Row>
        <div class="project-member-add">
          <Row
            class="member-row"
            type="flex"
            align="middle"
            justify="center"
            v-for="(member, idx) of projectDetail.teamEditInfo.members"
            :key="idx">
            <Col class="member-col" :span="14">
              <img
                class="member-add-avatar"
                alt="头像"
                :src="member.avatar ? $avatarUrl.replace('avatarID', member.avatar) : '@/assets/logo.png'">
                {{member.name}}
            </Col>
            <Col class="member-col" :span="2">
              <Checkbox
                :disabled="member.id === userInfo.id"
                v-model="member.inProject">
              </Checkbox>
            </Col>
            <Col class="member-col" :span="8">
              <Select
                size="small"
                :disabled="member.id === userInfo.id"
                v-if="member.inProject"
                :transfer="true"
                v-model="member.userType"
                style="width:6rem">
                <Option v-for="(option, index) in userTypeList" :value="option.code" :key="index">
                  {{option.name}}
                </Option>
              </Select>
            </Col>
          </Row>
        </div>
      </div>
      <div slot="footer">
        <Button type="text" @click.stop="clearTeamEditInfo">取消</Button>
        <Button type="primary" @click.stop="setTeamInfo">确定</Button>
      </div>
    </Modal>
    <Modal
      v-model="projectDetail.addShow"
      title="添加项目成员"
      :width="720"
      :closable="false"
      :mask-closable="false"
      :transfer="true">
      <div class="project-member-add">
        <Row
          class="member-row"
          v-for="(member, idx) of this.projectDetail.addList"
          :key="member.key"
          type="flex"
          align="middle">
          <Col class="member-col" :span="2">
          #{{ idx + 1 }}
          </Col>
          <Col class="member-col" :span="12">
            <img
              class="member-add-avatar"
              :src="member.info.avatar ? $avatarUrl.replace('avatarID', member.info.avatar) : '@/assets/logo.png'">
            <Select
              :remote-method="remoteGetReadyList"
              :loading="projectDetail.loading"
              v-model="member.id"
              ref="memberSelect"
              @on-open-change="changeOpen($event, idx, member)"
              @on-change="setAddMemberInfo(member)"
              placeholder="请输入手机号"
              :transfer="true"
              :title="member.info.name || ''"
              remote
              filterable
              clearable>
              <Option
                v-for="item of projectDetail.readyList"
                :value="item.id"
                :label="item.name"
                class="member-option"
                :key="item.id">
                <img
                  :src="item.avatar ? $avatarUrl.replace('avatarID', item.avatar) : '@/assets/logo.png'"
                  class="member-option-avatar" />
                  <span class="member-option-name" :title="item.name">
                    {{ item.name }}
                  </span>
                  {{ item.phone }}
              </Option>
            </Select>
          </Col>
          <Col class="member-col" :span="6">
            <Select
              :transfer="true"
              v-model="member.userType"
              style="width:6rem">
              <Option v-for="(option, index) in userTypeList" :value="option.code" :key="index">
                {{option.name}}
              </Option>
            </Select>
          </Col>
          <Col class="member-col" :span="2">
            <Button type="primary" size="small" @click.stop="addProjectMember(idx + 1)">添加</Button>
          </Col>
          <Col class="member-col" :span="2">
            <Button
              v-if="projectDetail.addList.length > 1"
              type="default"
              size="small"
              @click.stop="removeAddMember(idx)">
              删除
            </Button>
          </Col>
        </Row>
      </div>
      <div slot="footer">
        <Button type="text" @click.stop="clearMemberAddInfo">取消</Button>
        <Button type="primary" @click.stop="confirmAddMember">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import orgApi from '@/api/org'
import { getUserType } from '@/api/invite'
import projectApi from '@/api/project'
import uuidv4 from 'uuid/v4'

export default {
  data () {
    return {
      teamList: [], // 团队列表
      userTypeList: [],
      menuSelected: {
        rangeType: 1,
        orgId: ''
      },
      constMap: {
        personal: 1,
        company: 2,
        team: 3,
        personalTeam: 1, // 我的个人团队
        companyTeam: 2, // 我的公司团队
        allTeam: 3, // 我的所有团队
        admin: 0, // 超管
        manager: 1, // 管理员
        member: 2 // 普通成员
      },
      projectList: [],
      memberList: [],
      newProject: {
        show: false,
        enter: true,
        info: {
          name: '',
          rangeType: '',
          orgId: '',
          type: 1
        },
        addLock: false
      },
      projectMenu: {
        showId: 0,
        menuItems: [{
          title: '详情',
          click: this.showProjectDetail
        },
        {
          title: '删除',
          click: this.tryDeleteProject
        }]
      },
      projectDetail: {
        show: false,
        project: {},
        infoChanged: false, // 基本信息修改标记
        team: {},
        teamEditShow: false,
        teamEditInfo: {
          readyTeam: [],
          orgId: '',
          projectId: 0,
          members: []
        },
        memberList: [],
        addList: [],
        readyList: [],
        addShow: false,
        loading: false, // 远程获取成员列表中
        timerIdx: 0
      },
      turnToProject: 0
    }
  },
  computed: {
    userInfo () {
      return this.projectDetail.memberList.find(item => this.$store.state.user.userInfo.id === item.id) || {}
    },
    roleInfo () {
      return this.memberList.find(item => this.$store.state.user.userInfo.id === item.id) || {}
    }
  },
  mounted () {
    this.refreshPage()
  },
  methods: {
    refreshPage () {
      this.getUserType()
      this.getMemberList()
      this.getTeamList()
      this.selectMenu()
    },
    async getTeamList () {
      let personalTeamRequest = orgApi.getTeamList({ rangeType: 1 })
      let companyTeamRequest = orgApi.getTeamList({ rangeType: 2 })
      let personalTeam = await personalTeamRequest
      let companyTeam = await companyTeamRequest
      this.teamList = [...personalTeam, ...companyTeam]
    },
    async getTeamMemberList (id) {
      let params = {
        orgId: id
      }
      let data = await orgApi.getTeamMembers(params)
      return data
    },
    async getUserType () {
      await getUserType().then(resp => {
        if (resp.response.success) {
          this.userTypeList = resp.data
        }
      })
    },
    async getMemberList () {
      let data = await orgApi.getMemberList()
      if (data) {
        this.memberList = data
      }
    },
    tryAddProject () {
      this.newProject.show = true
      this.$nextTick(() => {
        this.$refs.newProjectInput.focus()
      })
    },
    async confirmAddProject () {
      if (this.newProject.addLock) {
        this.$Message.success('正在保存...')
        return
      }
      this.newProject.addLock = true
      Object.assign(this.newProject.info, this.menuSelected)
      let data = await projectApi.addProject(this.newProject.info)
      if (data) {
        this.$Message.success('添加成功')
        if (this.newProject.enter) {
          this.openProject(data.id)
        } else {
          this.clearNewProjectInfo()
          this.selectMenu()
        }
      } else {
        this.$Message.error('添加失败')
        this.$refs.newProjectInput.focus()
      }
      this.newProject.addLock = false
    },
    openProject (projectId) {
      if (this.projectMenu.showId === projectId) return
      this.$router.push({
        name: 'Page',
        params: {
          projectId
        }
      })
    },
    tryDeleteProject (project) {
      let text = `你确定删除项目${project.name}吗？`
      this.$confirm(text, this.confirmDeleteProject, project.id)
    },
    async confirmDeleteProject (id) {
      let params = { ids: [id] }
      let data = await projectApi.deleteProject(params)
      if (data) {
        this.selectMenu()
      } else {
        this.$Message.error('刪除失败')
      }
    },
    clearNewProjectInfo () {
      this.newProject = {
        show: false,
        enter: true,
        info: {
          title: '',
          rangeType: '',
          orgId: '',
          type: 1
        }
      }
    },
    async getProjectList () {
      let params = {
        ...this.menuSelected,
        type: 1
      }
      let data = await projectApi.getProjectList(params)
      if (data) {
        this.projectList = data
        let project = null
        if (this.turnToProject) {
          project = this.projectList.find(item => item.id === this.turnToProject)
          this.turnToProject = 0
        }
        if (project) {
          this.showProjectDetail(project)
        }
      } else {
        this.$Message.error('获取项目列表失败')
      }
    },
    selectMenu (rangeType, orgId) {
      if (rangeType !== this.menuSelected.rangeType) {
        this.hideProjectDetail()
      }
      if (rangeType !== undefined) {
        this.menuSelected = {
          rangeType,
          orgId: orgId || ''
        }
      }
      this.getProjectList()
    },
    showProjectMenu (project) {
      this.projectMenu.showId = project.id
    },
    hideProjectMenu () {
      this.projectMenu.showId = 0
    },
    clickProjectMenu (item, project) {
      this.hideProjectMenu()
      if (item.click) {
        item.click(project)
      }
    },
    async showProjectDetail (project) {
      this.projectDetail.show = true
      this.projectDetail.project = project
      // this.$bus.$emit('hide-home-menu')
      let params = {
        projectId: project.id
      }
      let data = await projectApi.getProjectMembers(params)
      if (data) {
        this.projectDetail.memberList = data
      } else {
        this.$Message.error('获取成员列表失败')
      }
    },
    hideProjectDetail () {
      this.projectDetail.show = false
      // this.$bus.$emit('show-home-menu')
      this.projectDetail.project = {}
    },
    setMembers (project) {
      //
    },
    setToCompany () {
      let sText = '个人项目移动到公司为不可逆操作，确定移动项目到公司吗？'
      this.$confirm(sText, async () => {
        let data = await projectApi.setToCompany({ projectId: this.projectDetail.project.id })
        if (data) {
          this.$Message.success('设置成功')
          this.showProjectDetail(this.projectDetail.project)
        } else {
          this.$Message.error('设置失败')
        }
      })
    },
    setTeam () {
      this.projectDetail.teamEditShow = true
      this.projectDetail.teamEditInfo.orgId = this.projectDetail.project.orgId
      this.getReadyTeam()
    },
    async getReadyTeam () {
      let params = {
        projectId: this.projectDetail.project.id
      }
      let data = await projectApi.getProjectReadyTeamList(params)
      if (data) {
        this.projectDetail.teamEditInfo.readyTeam = data
      } else {
        this.$Message.error('加载团队列表失败')
      }
      let team = this.projectDetail.teamEditInfo.readyTeam.find(item => item.id === this.projectDetail.project.orgId)
      if (team) {
        this.getTeamMembers()
      }
    },
    async getTeamMembers () {
      if (!this.projectDetail.teamEditInfo.orgId) return
      let data = await orgApi.getTeamMembers({ orgId: this.projectDetail.teamEditInfo.orgId })
      if (data) {
        this.projectDetail.teamEditInfo.members = []
        for (let member of data) {
          let info = {
            name: member.name,
            avatar: member.avatar,
            id: member.id,
            inProject: false,
            userType: 2
          }
          let memData = this.projectDetail.memberList.find(item => item.id === member.id)
          if (memData) {
            info.inProject = true
            info.userType = memData.userType
          }
          this.projectDetail.teamEditInfo.members.push(info)
        }
      } else {
        this.$Message.error('加载团队成员列表失败')
      }
    },
    async setTeamInfo () {
      let params = {
        members: [],
        orgId: this.projectDetail.teamEditInfo.orgId,
        projectId: this.projectDetail.project.id
      }
      for (let info of this.projectDetail.teamEditInfo.members) {
        if (!info.inProject) continue
        let memData = {
          id: info.id,
          userType: info.userType
        }
        params.members.push(memData)
      }
      let data = await projectApi.setOrgInfo(params)
      if (data) {
        this.clearTeamEditInfo()
        this.turnToProject = this.projectDetail.project.id
        this.getProjectList()
      } else {
        this.$Message.error('设置失败')
      }
    },
    clearTeamEditInfo () {
      this.projectDetail.teamEditShow = false
      this.projectDetail.teamEditInfo = {
        readyTeam: [],
        orgId: '',
        projectId: 0,
        members: []
      }
    },
    changeProjectInfo () {
      this.projectDetail.infoChanged = true
    },
    async saveProjectInfo () {
      if (!this.projectDetail.infoChanged) return
      let data = await projectApi.updateProject(this.projectDetail.project)
      if (data) {
        this.$Message.success('修改成功')
        this.projectDetail.infoChanged = false
      } else {
        this.$Message.error('修改内容保存失败')
        this.turnToProject = this.projectDetail.project.id
        this.getProjectList()
      }
    },
    addProjectMember (idx) {
      let data = {
        id: 0,
        userType: this.constMap.member,
        info: {},
        key: uuidv4()
      }
      this.projectDetail.addShow = true
      if (!idx) {
        this.projectDetail.addList = [data]
      } else {
        this.projectDetail.addList.splice(idx, 0, data)
      }
    },
    removeAddMember (idx) {
      if (this.projectDetail.addList.length > 1) {
        this.projectDetail.addList.splice(idx, 1)
      }
    },
    async confirmAddMember () {
      let members = []
      for (let member of this.projectDetail.addList) {
        if (member.id) {
          let data = {
            id: member.id,
            userType: member.userType
          }
          members.push(data)
        }
      }
      if (!members.length) {
        this.$Message.warning('未填写任何有效数据')
        return
      }
      let params = {
        projectId: this.projectDetail.project.id,
        members
      }
      let data = await projectApi.addProjectMembers(params)
      if (data) {
        this.$Message.success('添加成功')
        this.clearMemberAddInfo()
      } else {
        this.$Message.error('添加失败')
      }
      this.showProjectDetail(this.projectDetail.project)
    },
    clearMemberAddInfo () {
      this.projectDetail.addShow = false
      this.projectDetail.addList = []
    },
    setAddMemberInfo (member) {
      let info = this.projectDetail.readyList.find(item => item.id === member.id)
      member.info = info || {}
    },
    tryRemoveProjectmember (member) {
      let self = (member.id === this.userInfo.id)
      if (member.userType === this.constMap.manager) {
        let list = this.projectDetail.memberList.filter(item => item.userType === this.constMap.manager)
        if (list.length <= 1) {
          let text = '该成员是最后项目一名管理员，请先转让权限'
          if (self) {
            text = '您是项目最后一名管理员，请先转让权限'
          }
          this.$Message.warning(text)
          this.showProjectDetail(this.projectDetail.project)
          return
        }
      }
      let text = `确认将成员${member.name}移出项目${this.projectDetail.project.name}吗？`
      if (self) {
        text = `您确认退出项目${this.projectDetail.project.name}吗`
      }
      this.$confirm(text, this.confirmRemoveProjectMember, member.id, self)
    },
    async confirmRemoveProjectMember (id, self) {
      let params = {
        ids: [id],
        projectId: this.projectDetail.project.id
      }
      let data = await projectApi.removeProjectMember(params)
      if (data) {
        let text = self ? '退出成功' : '删除成功'
        this.$Message.success(text)
        this.showProjectDetail(this.projectDetail.project)
      } else { // 刪除失敗可能是此人已不在项目，应该刷新页面并尝试恢复现场
        let text = self ? '退出失败' : '删除失败'
        this.$Message.error(text)
        this.turnToProject = this.projectDetail.project.id
        this.getProjectList()
      }
    },
    changeOpen (open, idx, member) {
      if (open) {
        this.projectDetail.readyList = []
        let text = member.info.phone || ''
        if (text) {
          this.$refs.memberSelect[idx].setQuery(text)
        } else {
          this.remoteGetReadyList(text)
        }
      }
    },
    remoteGetReadyList (query) {
      if (this.projectDetail.timerIdx) {
        clearTimeout(this.projectDetail.timerIdx)
      }
      this.projectDetail.timerIdx = setTimeout(async () => {
        this.projectDetail.timerIdx = 0
        let params = { keywords: query }
        this.projectDetail.loading = true
        let data = await projectApi.getUserListByKeyword(params)
        this.projectDetail.loading = false
        if (data) {
          let excludeList = []
          this.projectDetail.memberList.forEach(item => excludeList.push(item.id))
          this.projectDetail.addList.forEach(item => excludeList.push(item.id))
          let list = data.filter(item => !excludeList.includes(item.id))
          this.projectDetail.readyList = list
        } else {
          this.$Message.error('获取用户列表失败，可能是网络挂了')
        }
      }, 200)
    },
    async changePermission (member) {
      if (member.userType === this.constMap.member) {
        let list = this.projectDetail.memberList.filter(item => item.userType === this.constMap.manager)
        if (list.length < 1) {
          this.$Message.warning('团队里至少有一个管理员')
          this.showProjectDetail(this.projectDetail.project)
          return
        }
      }
      let params = {
        ids: [member.id],
        projectId: this.projectDetail.project.id,
        userType: member.userType
      }
      let data = await projectApi.setMemberPermission(params)
      if (data) {
        this.$Message.success('修改成功')
      } else {
        this.$Message.error('修改失败')
      }
    }
  }
}
</script>

