<template>
  <div class="answer-record-page" ref="answerRecord">
    <div
      class="answer-record-filter"
      v-if="!recordId">
      <div class="filter-title">根项目：</div>
      <Select v-model="rootProject" class="filter-select">
        <Option v-for="(option, idx) in projectList" :value="option.id" :key="idx">
          {{ option.name }}
        </Option>
      </Select>
      <div class="filter-title">子项目：</div>
      <Select v-model="childProject" class="filter-select">
        <Option v-for="(option, idx) in childrenList" :value="option.id" :key="idx">
          {{ option.name }}
        </Option>
      </Select>
    </div>
    <div class="table" v-if="!recordId" ref="table">
      <Table
        :height="tableHeight"
        :columns="columns"
        :data="recordList"
        :width="size.width"
        border>
      </Table>
    </div>
    <div class="detail" v-else>
      <div class="detail-menu">
        <div class="project-data">
          <div class="project-name" @click="pushToPage">
            {{projectInfo.name}}
          </div>
            的做题记录
        </div>
        <div class="answer-menu">
          <div>设为测试用例</div>
        </div>
      </div>
      <div class="detail-content">
        <div class="answer-record">
          <div class="answer-record-title">
            做题记录
          </div>
          <Tree
            :treeData="recordDetail"
            class="answer-record-tree"
            :nodeClass="'tree-node-container'"
            :expand="false"
            v-if="recordDetail.length">
            <div
              class="node-render-content"
              slot-scope='{ data, parentData}'
              :class="{ 'root-node-content': parentData.isRoot }">
              <span v-if="parentData.isRoot" class="root-node-title">
                {{ parentData.children.findIndex(item => item.id === data.id) + 1 + '. '}}
              </span>
              {{ data.title }}
            </div>
          </Tree>
          <div class="notice" v-else>
            无详细记录信息
          </div>
        </div>
        <div class="report">
          <div class="report-title">报告</div>
          <div class="report-content">
            <div class="content" v-html="reportContent"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import question from '@/api/question'
import Tree from '@/components/simple-tree'
import showdown from 'showdown'
import projectApi from '@/api/project'

export default {
  components: {
    Tree
  },
  data () {
    return {
      projectInfo: {},
      recordList: [],
      columns: [
        {
          title: '项目名称',
          key: 'projectName',
          sortable: true
        },
        {
          title: '客户',
          key: 'customer',
          sortable: true
        },
        {
          title: '用户标识',
          key: 'userId',
          sortable: true
        },
        {
          title: '联系电话',
          key: 'phone',
          sortable: true
        },
        {
          title: '设备',
          key: 'device',
          sortable: true
        },
        {
          title: 'IP',
          key: 'ip',
          sortable: true
        },
        {
          title: '做题时间',
          key: 'updateTime',
          sortable: true
        },
        {
          title: '做题时长',
          key: 'time',
          sortable: true
        },
        {
          title: '步骤',
          key: 'step',
          sortable: true
        },
        {
          title: '操作',
          key: 'action',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary'
                },
                on: {
                  click: () => {
                    this.showRecordDetail(params.index)
                  }
                }
              }, '详情')
            ])
          }
        }
      ],
      size: {
        width: 0,
        height: 0
      },
      tableHeight: 0,
      recordDetail: [],
      reportContent: '',
      converter: null,
      projectList: [
        {
          id: 'default',
          name: '全部项目'
        }
      ],
      childrenList: [
        {
          id: 'default',
          name: '全部子项目'
        }
      ],
      childProject: 'default',
      rootProject: 'default'
    }
  },
  computed: {
    recordId () {
      return this.$route.query.recordId || ''
    },
    all () {
      return this.childProject === 'default'
    },
    lookProject () {
      let project = this.childProject
      if (project === 'default') {
        project = this.rootProject === 'default' ? '' : this.rootProject
      }
      return project
    }
  },
  watch: {
    recordId () {
      if (this.recordId) {
        this.calSize()
        this.getRecordDetail()
        this.getReportContent(this.recordId)
        this.getProjectDetail()
      }
    },
    rootProject (newValue, oldValue) {
      if (newValue !== oldValue) {
        this.getChildProjectList()
      }
    },
    lookProject: {
      immediate: true,
      handler () {
        this.$nextTick(() => {
          this.getRecordList()
        })
      }
    }
  },
  mounted () {
    this.childProject = this.$route.query.projectId || 'default'
    this.rootProject = this.$route.query.rootId || 'default'
    if (!this.converter) {
      this.converter = new showdown.Converter({ literalMidWordUnderscores: true })
    }
    this.calSize()
    this.getProjectList()
    window.addEventListener('resize', this.calSize)
    if (this.recordId) {
      this.getRecordDetail()
      this.getReportContent(this.recordId)
    }
    this.getProjectDetail()
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.calSize)
  },
  methods: {
    async getProjectList () {
      let params = {}
      let data = await projectApi.getRootProjectList(params)
      if (data) {
        this.projectList = [
          {
            id: 'default',
            name: '全部项目'
          }
        ]
        this.projectList.push(...data)
      }
    },
    async getChildProjectList () {
      this.childrenList = [
        {
          id: 'default',
          name: '全部子项目'
        }
      ]
      if (this.rootProject && this.rootProject !== 'default') {
        let params = {
          id: this.rootProject
        }
        let data = await projectApi.getProjectList(params)
        if (data) {
          let idx = data.findIndex(item => item.id === this.rootProject)
          if (idx !== -1) {
            let item = data.splice(idx, 1)
            data.unshift(...item)
          }
          this.childrenList.push(...data)
        }
        if (data.findIndex(item => item.id === this.childProject) === -1) {
          this.childProject = 'default'
        }
      }
    },
    async getRecordList () {
      let params = {
        projectId: this.lookProject
      }
      let list = await question.getHistoryAnswerRecord.apply(this, [params])
      for (let data of list) {
        data.time = Math.round((data.updateTime - data.createTime) / 1000) + ' s'
        data.updateTime = this.formatTime(data.updateTime)
      }
      this.recordList = list || []
    },
    async getProjectDetail () {
      if (this.$route.query.detailId) {
        this.projectInfo = await projectApi.getProjectDetail({ id: this.$route.query.detailId })
      }
    },
    showRecordDetail (index) {
      let data = this.recordList[index]
      this.$router.push({
        name: 'AnswerRecord',
        query: {
          detailId: data.projectId,
          recordId: data.id
        }
      })
    },
    async getRecordDetail () {
      let params = {
        recordId: this.recordId
      }
      let record = await question.getAnswerRecord.apply(this, [params])
      if (record) {
        this.recordDetail = record
      } else {
        this.$Message.error('获取做题记录失败')
      }
    },
    async getReportContent (id) {
      let params = {
        recordId: id
      }
      let recordText = await question.getReportContent.apply(this, [params])
      this.reportContent = this.converter.makeHtml(recordText)
    },
    calSize () {
      this.$nextTick(() => {
        let rect = this.$refs.answerRecord.getBoundingClientRect()
        this.size = {
          width: rect.width,
          height: rect.height
        }
        if (this.$refs.table) {
          rect = this.$refs.table.getBoundingClientRect()
          this.tableHeight = rect.height
        }
      })
    },
    formatTime (timeStamp) {
      let formatNum = (num) => {
        return num < 10 ? '0' + num : num
      }
      let date = new Date(timeStamp)
      let year = date.getFullYear()
      let month = formatNum(date.getMonth() + 1)
      let day = formatNum(date.getDay())
      let hour = formatNum(date.getHours())
      let minute = formatNum(date.getMinutes())
      let sText = `${year}-${month}-${day} ${hour}:${minute}`
      return sText
    },
    pushToPage (id) {
      this.$router.push({
        name: 'Page',
        params: {
          projectId: this.rootProject
        },
        query: {
          currentProject: id
        }
      })
    }
  }
}
</script>

<style lang="stylus">
.answer-record-page
  display flex
  flex-direction column
  .answer-record-filter
    min-height 3rem
    display flex
    align-items center
    .filter-title
      padding .5rem
    .filter-select
      margin .5rem
      width 10rem
  .table
    flex auto
  .detail
    background white
    display flex
    flex-direction column
    height 100%
    .detail-menu
      background #B0C8F4
      padding 0 .5rem
      font-size 1rem
      min-height 2rem
      display flex
      align-items center
      user-select none
      .project-data
        display flex
        align-items center
        padding-right .5rem
        margin-right .5rem
        border-right 1px solid black
        .project-name
          color blue
          cursor pointer
          &:hover
            text-decoration underline
          &:active
            position relative
            left 1px
            top 1px
    .detail-content
      flex auto
      display flex
    .answer-record-title, .report-title
      user-select none
      background #F7F7F7
      position absolute
      width 100%
      top 0
      left 0
      font-size 1.5rem
      text-align center
    .answer-record
      box-shadow -1px 1px 0 0 #4573DE inset
      width 50%
      padding 1rem
      overflow auto
      padding-top 2.5rem
      position relative
      .answer-record-tree
        margin-top 1rem
        >.tree-node-container
          margin 0 auto
          margin-bottom 0.5rem
          padding 0.5rem
          box-shadow 0 0 2px 1px #A1BFFC
          border-radius 4px
        .root-node-content
          font-size 1rem
          font-weight bold
      .notice, .link
        text-align center
        margin .5rem
    .report
      width 50%
      position relative
      box-shadow 1px 1px 0 0 #4573DE inset
      padding 1rem
      padding-top 2.5rem
      overflow auto
</style>
