<template lang="pug">
Layout.home-page
  .content
    .content-home
      .content-left(:class="menuShow ? '' : 'content-left-hide-menu'")
        ul.type-list(v-show='menuShow')
          li(v-for='(item, key) in showItems', :class="{\
          'type-list-item': true,\
          'choose-show': showType === item.name\
          }", @click='setShowType(item.name)', :key='key')
            i.icon
              Icon(:type='item.icon')
            |               {{ item.title }}
          li.type-list-item(@click="$router.push({ path: '/layout/knowledge' })")
            i.icon
              Icon(type='ios-book')
            |               knowledge
          li.type-list-item(@click="$router.push({ path: '/answerRecord' })")
            i.icon
              Icon(type="md-book")
            | 咨询记录
        .menu-fold(@click.stop='menuShow=!menuShow')
          Icon.show-icon(type='ios-arrow-left', title='隐藏', v-if='menuShow')
          Icon.show-icon(title='展开', type='ios-arrow-right', v-else='')
      .content-right
        template(v-if="showType === 'project'")
          ProjectBoard
        template(v-if="showType === 'member'")
          Member
        template(v-if="showType === 'orgnization'")
          Orgnization
        template(v-if="showType === 'customComponent'")
          CustomComponentManage
        template(v-if="showType === 'networkDisk'")
          NetworkDisk
</template>

<script>
import OrgBoard from '@/components/org-board'
import Orgnization from '@/components/orgnization'
import Member from '@/components/member'
import CustomComponentManage from '@/components/custom-component/manage'
import ProjectBoard from '@/components/project-board'
import NetworkDisk from '@/components/network-disk'
import AnswerRecord from '@/pages/AnswerRecord'

export default {
  name: 'home-page',
  components: {
    OrgBoard,
    Orgnization,
    Member,
    CustomComponentManage,
    ProjectBoard,
    NetworkDisk,
    AnswerRecord
  },
  data () {
    return {
      showItems: [{
        name: 'project',
        title: '项目',
        icon: 'md-clipboard'
      },
      {
        name: 'orgnization',
        title: '团队',
        icon: 'md-people'
      },
      {
        name: 'member',
        title: '成员',
        icon: 'md-contacts'
      },
      {
        name: 'networkDisk',
        title: '网盘',
        icon: 'md-cloud'
      },
      {
        name: 'customComponent',
        title: '自定义组件管理',
        icon: 'md-cube'
      }],
      menuShow: true
    }
  },
  computed: {
    showType () {
      return this.$route.query.type || 'project'
    }
  },
  watch: {
    showType () {
      let data = this.showItems.find(item => item.name === this.showType)
      if (data) {
        this.$setTitle(data.title)
      }
    }
  },
  methods: {
    setShowType (sType) {
      this.$router.push({ name: 'Home', query: { type: sType } })
    },
    showMenu () {
      this.menuShow = true
    },
    hideMenu () {
      this.menuShow = false
    }
  },
  created () {
    this.$bus.$on('show-home-menu', this.showMenu)
    this.$bus.$on('hide-home-menu', this.hideMenu)
  },
  destroyed () {
    this.$bus.$off('show-home-menu', this.showMenu)
    this.$bus.$off('hide-home-menu', this.hideMenu)
  }
}
</script>

