<template>
  <div class="member-page">
    <div class="title">成员</div>
    <div class="member-list">
      <div class="member-menu">
        <div class="member-menu-item">
          <span>排序方式:</span>
          <Select
            size="small"
            style="width:100px"
            v-model="sortInfo.sortType">
            <Option
              v-for="(option, idx) of sortOptions"
              :key="idx"
              :value="option.value"> {{ option.label }}
            </Option>
          </Select>
          <div class="sort-swap" title="逆序" @click.stop="sortInfo.reverse=-1 * sortInfo.reverse">
            <Icon type="md-swap" class="sort-swap-icon"></Icon>
          </div>
        </div>
        <div class="member-menu-item" v-if="isManager">
          <Button size="small" type="success" class="member-add-button" @click.stop="showInviteDialog">添加成员</Button>
        </div>
        <div class="member-menu-item" v-if="isManager">
          <Tooltip placement="top" :transfer="true" content="给所有未激活用户发送激活邮件">
            <Button size="small" type="success" @click.stop="sendAllEmail">发送所有激活邮件</Button>
          </Tooltip>
        </div>
      </div>
      <div class="members">
        <div
          :class="{
            member: true,
            'member-self': userInfo.id === member.id
          }"
          v-for="(member, idx) of memberList"
          :key="idx">
          <div class="user-avatar">
            <img
              class="avatar"
              :src="member.avatar ? $avatarUrl.replace('avatarID', member.avatar) : '@/assets/logo.png'"
              alt="用户头像" />
            <div class="user-name" v-if="member.name">{{ member.name }}</div>
          </div>
          <div class="user-info">
            <div class="info-item">
              <Icon class="info-icon" type="ios-mail"></Icon>
              <span class="info-content">{{ member.email }}</span>
            </div>
            <div class="info-item">
              <Icon class="info-icon" type="md-call"></Icon>
              <span class="info-content">{{ member.phone || '未设置'}}</span>
            </div>
            <div class="info-item" v-if="!member.state">
              <Icon class="info-icon state" type="md-checkmark"></Icon>
              <span class="info-content">已认证</span>
            </div>
            <div class="info-item" v-else>
              <Icon class="info-icon un-state" type="md-alert"></Icon>
              <span class="info-content">未认证</span>
            </div>
            <div class="info-item" v-if="member.userType === constMap.admin || member.userType === constMap.manager">
              <Icon class="info-icon" type="md-person"></Icon>
              <span class="info-content">管理员</span>
            </div>
          </div>
          <div class="menu">
            <Select
              v-if="isManager && member.id !== userInfo.id && member.userType !== constMap.admin"
              :transfer="true"
              :value="member.userType"
              @on-change="setTobeManager(member, $event)"
              style="width:6rem">
              <Option v-for="(option, index) in userTypeList" :value="option.code" :key="index">
                {{option.name}}
              </Option>
            </Select>
            <Button
              v-if="isManager && member.state"
              :disabled="emailSentList.includes(member.id)"
              @click.stop="emailAuthenticate([member.id])"
              class="email-authenticate"
              type="primary">
              激活邮件
            </Button>
            <Button
              @click.stop="editMemberInfo(member)"
              class="edit-user"
              type="primary"
              v-if="(isManager && member.userType !== constMap.admin) || userInfo.id === member.id">
              编辑
            </Button>
            <Button
              @click.stop="deleteMember(member)"
              v-if="isManager && member.userType !== constMap.admin && userInfo.id !== member.id"
              type="default">
              删除
            </Button>
          </div>
        </div>
      </div>
    </div>
    <Modal
      :width="isLocalNetwork ? '640' : '440'"
      :mask-closable="false"
      @on-cancel="clearInviteeInfo"
      v-model="showInvite"
      title="邀请用户"
      :closable="false">
      <div @keyup.ctrl.enter.stop="addInvitee()">
        <Layout
          class="invite">
          <Row class="invite-header">
            <Row class="network-row">
              <h4>请选择系统使用的网络环境</h4>
              <RadioGroup v-model="networkType">
                <Radio :label="1">
                  <!-- <Icon type="social-apple"></Icon> -->
                  <span>内网</span>
                </Radio>
                <Radio :label="2">
                  <!-- <Icon type="social-android"></Icon> -->
                  <span>外网</span>
                </Radio>
              </RadioGroup>
              <p>当前共填写{{inviteeList.length}}人</p>
            </Row>
          </Row>
          <Row class="invite-body">
            <div ref="userScroll" class="invite-scroll">
              <template v-for="(item, index) in inviteeList">
                <Row
                  class="user-row"
                  type="flex"
                  :key="index">
                  <Col class="user-row-cl">
                    <div
                      :class="{
                        'un-complete': isLocalNetwork && (item.name || item.phone) && (!item.name || !item.phone),
                        'complete': (isLocalNetwork && item.name && item.phone) || (isInternetNetwork && item.email)
                      }">
                      #{{index + 1}}
                    </div>
                  </Col>
                  <template v-if="isInternetNetwork">
                    <Col class="user-row-cl">
                      <Input
                        @on-keyup="checkKeyUp"
                        class="invitee__input"
                        :id="'input__' + index + '__1'"
                        v-model="item.email"
                        placeholder="请输入电子邮箱"/>
                    </Col>
                  </template>
                  <template v-else>
                    <Col class="user-row-cl">
                      <Input
                        @on-keyup="checkKeyUp"
                        class="invitee__input"
                        :id="'input__' + index + '__2'"
                        v-model="item.name"
                        placeholder="请输入姓名"/>
                    </Col>
                    <Col class="user-row-cl">
                      <Input
                        @on-keyup="checkKeyUp"
                        class="invitee__input"
                        :id="'input__' + index + '__3'"
                        v-model="item.phone"
                        placeholder="请输入电话号码"/>
                    </Col>
                  </template>
                  <Col class="user-row-cl">
                    <Select v-model="item.userType" style="width:6rem">
                      <Option v-for="(option, index) in userTypeList" :value="option.code" :key="index">
                        {{option.name}}
                      </Option>
                    </Select>
                  </Col>
                  <Col class="user-row-cl">
                    <Tooltip
                      :transfer="true"
                      placement="top"
                      content="Ctrl+Enter">
                      <Button type="primary" @click.stop="addInvitee(index)">
                        <Icon type="md-add"></Icon>
                      </Button>
                    </Tooltip>
                  </Col>
                  <Col class="user-row-cl">
                    <Button type="default"
                      :style="{
                        opacity: inviteeList.length > 1 ? 1 : 0
                      }"
                      @click.stop="deleteInvitee(index)">
                      <Icon type="md-remove"></Icon>
                    </Button>
                  </Col>
                </Row>
              </template>
            </div>
          </Row>
        </Layout>
      </div>
      <div slot="footer">
        <Button type="text" @click="clearInviteeInfo">取消</Button>
        <Button type="primary" @click.stop="submitInviteeInfo">提交</Button>
      </div>
    </Modal>
    <Modal
      v-model="editing.show" v-show="!editing.rebind.show"
      :closable="false">
      <div slot="header" class="setting-header">
        编辑资料
      </div>
      <div slot="footer" class="setting-footer">
        <Button type="text" @click.stop="clearEditingInfo">取消</Button>
        <Button type="primary" @click.stop="saveEditingInfo">确定</Button>
      </div>
      <div class="setting-content">
        <Form :model="editing.info" :label-width="80">
          <FormItem label="头像" class="form-item" v-if="!editing.info.state">
            <Row type="flex" class="form-row">
              <Col :span="grid.left" class="form-col">
                <img
                  :src="$avatarUrl.replace('avatarID', editing.info.avatar)"
                  alt="用户头像"
                  class="setting-avatar"
                  v-if="editing.info.avatar">
                  <div
                    class="avatar-bomb"
                    @click.stop="selectImage"
                    v-else>
                    点击上传头像
                  </div>
                  <input
                    type="file"
                    accept="image/*"
                    name="avatarInput"
                    id="avatarInput"
                    v-show="false"
                    @change="uploadImage"
                    ref="avatarInput">
              </Col>
              <Col :span="grid.right"></Col>
            </Row>
          </FormItem>
          <FormItem label="姓名" v-if="!editing.info.state">
            <Row type="flex" class="form-row">
              <Col :span="grid.left">
                <Input v-model="editing.info.name" placeholder="请输入姓名" />
              </Col>
              <Col :span="grid.right"></Col>
            </Row>
          </FormItem>
          <FormItem label="手机号" v-if="isLocalNetwork || !editing.info.state">
            <Row type="flex">
              <Col :span="grid.left">
                <Input
                  :disabled="isInternetNetwork && !editing.info.state"
                  v-model="editing.info.phone" />
              </Col>
              <Col :span="grid.right" class="form-col">
                <Button
                  class="rebind-button"
                  type="primary"
                  @click.stop="rebind('phone')"
                  v-if="isInternetNetwork && !editing.info.state"
                  size="small">
                  重新绑定手机号
                </Button>
              </Col>
            </Row>
          </FormItem>
          <FormItem label="邮箱" v-if="isInternetNetwork">
            <Row type="flex">
              <Col :span="grid.left">
                <Input
                  v-model="editing.info.email"
                  :disabled="!editing.info.state" />
              </Col>
              <Col :span="grid.right" class="form-col">
                <Button
                  v-if="!editing.info.state"
                  class="rebind-button"
                  type="primary"
                  @click.stop="rebind('email')"
                  size="small">
                  重新绑定邮箱
                </Button>
              </Col>
            </Row>
          </FormItem>
          <!-- <FormItem label="绑定微信">
            <Row type="flex">
            </Row>
          </FormItem> -->
          <FormItem
            v-if="isManager && editing.info.id !== userInfo.id && editing.info.userType !== constMap.admin"
            label="权限">
            <Col :span="grid.left">
              <Select
                :transfer="true"
                v-model="editing.info.userType"
                style="width:6rem">
                <Option v-for="(option, index) in userTypeList" :value="option.code" :key="index">
                  {{option.name}}
                </Option>
              </Select>
            </Col>
          </FormItem>
        </Form>
      </div>
    </Modal>
    <Modal
      class="rebind-modal"
      v-model="editing.rebind.show"
      :mask-closable="false"
      :closable="false">
      <div slot="header" class="rebind-title">{{ editing.rebind.type === 'phone' ? '重新绑定手机号' : '重新绑定邮箱' }}</div>
      <div class="rebind-form">
        <Form :label-width="100">
          <FormItem label="请输入验证码">
            <Row type="flex">
              <Col :span="grid.left">
                <Input ref="rebindImage" v-model="editing.rebind.imageCode" placeholder="请输入图片验证码" />
              </Col>
              <Col :span="grid.right" class="form-col">
                <img class="image-code" :src="editing.rebind.verifyImage" alt="验证码" @click.stop="getNewVerifyImage">
              </Col>
            </Row>
          </FormItem>
          <FormItem
            v-if="editing.rebind.type === 'phone'"
            label="请输入新手机号">
            <Col :span="grid.left">
              <Input
                ref="rebindPhone"
                key="rebindPhone"
                v-model="editing.rebind.phone"
                placeholder="请输入新手机号" /></Input>
            </Col>
            <Col :span="grid.right">
              <Button
                type="primary"
                size="small"
                class="rebind-button"
                :disabled="editing.rebind.frozen"
                @click.stop="getVerifyCode">
                获取短信验证码
              </Button>
            </Col>
          </FormItem>
          <FormItem label="请输入新邮箱" v-else>
            <Col :span="grid.left">
              <Input
                ref="rebindEmail"
                key="rebindEmail"
                v-model="editing.rebind.phone"
                placeholder="请输入新邮箱账号" />
            </Col>
            <Col :span="grid.right">
              <Button
                type="primary"
                size="small"
                class="rebind-button"
                :disabled="editing.rebind.frozen"
                @click.stop="getVerifyCode">
                获取短信验证码
              </Button>
            </Col>
          </FormItem>
          <FormItem label="请输入短信验证码">
            <Row type="flex">
              <Col :span="grid.left">
                <Input v-model="editing.rebind.verifyCode" ref="rebindCode" placeholder="请输入验证码" />
              </Col>
              <Col :span="grid.right">
              </Col>
            </Row>
          </FormItem>
        </Form>
      </div>
      <div slot="footer" class="setting-footer">
        <Button type="text" @click.stop="editing.rebind.show=false">取消</Button>
        <Button type="primary" @click.stop="saveRebind">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import orgApi from '@/api/org'
import { getUserType, addUsers } from '@/api/invite'
import { uploadFile } from '@/api/file'
import { getCaptcha, validateCaptcha, sendSmsCode, sendEmailVcode } from '@/api/sms'
import { savePhone, saveEmail, saveAccountInfo } from '@/api/accountSetting'

const ADMIN = 0
const MANAGER = 1
const NORMAL_USER = 2

const NETWORK_LOCAL = 1
const NETWORK_INTERNET = 2

export default {
  name: 'member',
  data () {
    return {
      validateCaptcha,
      sendSmsCode,
      sendEmailVcode,
      /**
       * meberList成员 {}
       * email, id, name, avatar, phone
       * state  0 - 已认证 1- 未认证
       * userType 1-超级管理员 2-管理员 3-普通成员
       */
      constMap: {
        admin: ADMIN,
        manager: MANAGER,
        normalUser: NORMAL_USER,
        networkLocal: NETWORK_LOCAL,
        networkInternet: NETWORK_INTERNET
      },
      memberList: [],
      emailSentList: [],
      showInvite: false,
      inviteeList: [],
      networkType: NETWORK_INTERNET,
      userTypeList: [],
      sortInfo: {
        sortType: 'userType',
        reverse: 1
      },
      sortOptions: [{
        value: 'userType',
        label: '权限'
      },
      {
        value: 'state',
        label: '认证状态'
      }],
      editing: {
        show: false,
        info: {},
        rebind: {
          show: false,
          needVerified: true,
          frozen: false,
          imageCode: '',
          verifyCode: '',
          phone: '',
          email: '',
          type: '',
          verifyImage: '',
          imageUUID: ''
        }
      },
      grid: {
        left: 16,
        right: 8
      }
    }
  },
  computed: {
    userInfo () {
      return this.memberList.find(item => this.$store.state.user.userInfo.id === item.id) || {}
    },
    isLocalNetwork () {
      return this.networkType === NETWORK_LOCAL
    },
    isInternetNetwork () {
      return this.networkType === NETWORK_INTERNET
    },
    isAdmin () { // 超级管理员
      return this.userInfo.userType === ADMIN
    },
    isManager () { // 管理员
      return this.userInfo.userType === MANAGER || this.userInfo.userType === ADMIN
    },
    isNormalManager () { // 普通管理员
      return this.userInfo.userType === MANAGER
    },
    isNormalUser () {
      return this.userInfo.userType === NORMAL_USER
    }
  },
  mounted () {
    this.refreshPage()
    this.getUserType()
    // this.showInviteDialog()
  },
  methods: {
    async refreshPage () {
      let data = await orgApi.getMemberList()
      if (data) {
        this.memberList = data
        this.sortMemberList()
      }
    },
    sortMemberList () {
      let priorityList = ['userType', 'state']
      priorityList.sort((a, b) => b === this.sortInfo.sortType)
      this.memberList.sort((a, b) => {
        for (let priority of priorityList) {
          let deta = (a[priority] - b[priority]) * this.sortInfo.reverse
          if (deta) return deta
        }
        return 0
      })
    },
    sendAllEmail () {
      let list = []
      this.memberList.map(item => item.state && list.push(item.id))
      if (list.length) {
        this.emailAuthenticate(list)
      } else {
        this.$Message.success('用户列表中没有未认证成员')
      }
    },
    async emailAuthenticate (params) {
      let data = await orgApi.emailAuthenticate({ ids: params })
      if (data) {
        this.$Message.success('激活邮件发送成功')
        this.emailSentList.push(...params)
      } else {
        this.$Message.error('激活邮件发送失败')
      }
    },
    showInviteDialog () {
      this.showInvite = true
      this.addInvitee()
    },
    getUserType () {
      getUserType().then(resp => {
        if (resp.response.success) {
          this.userTypeList = resp.data
        } else {
          this.$Message.error('获取成员类型失败!')
        }
      })
    },
    addInvitee (idx) {
      let data = {
        email: '',
        phone: '',
        name: '',
        userType: MANAGER
      }
      if (idx) {
        this.inviteeList.splice(idx + 1, 0, data)
      } else {
        this.inviteeList.push(data)
      }
    },
    deleteInvitee (idx) {
      if (this.inviteeList.length <= 1) return
      this.inviteeList.splice(idx, 1)
    },
    submitInviteeInfo () {
      if (!this.checkForm()) return
      let inviteeList = this.filterEmptyInviteeData()
      if (!inviteeList.length) {
        this.$Message.error('未填写任何有效数据')
        return
      }
      const params = {
        networkType: this.networkType,
        users: inviteeList
      }
      addUsers(params).then(resp => {
        if (resp.response.success) {
          if (this.isInternetNetwork) {
            this.$Message.success({
              content: '已给所有新添加用户发送认证邮件！',
              duration: 5,
              closable: true
            })
          }
          this.clearInviteeInfo()
          this.refreshPage()
        }
      })
    },
    clearInviteeInfo () {
      this.showInvite = false
      this.inviteeList = []
    },
    checkForm () {
      for (let index in this.inviteeList) {
        let data = this.inviteeList[index]
        if (this.isLocalNetwork && (data.phone + data.name === (data.phone || data.name))) {
          this.$Message.error('信息未填写完整')
          return false
        }
      }
      return true
    },
    filterEmptyInviteeData () {
      let nonEmptyList = []
      for (let data of this.inviteeList) {
        if ((this.isLocalNetwork && data.phone && data.name.replace(/\s+/g, '')) || (this.isInternetNetwork && data.email.replace(/\s+/g, ''))) {
          nonEmptyList.push(data)
        }
      }
      return nonEmptyList
    },
    checkKeyUp (event) {
      if (this.$bus.inputing) return
      function getParentNode (node) {
        while (node !== document.body && !node.className.includes('invitee__input')) {
          node = node.parentNode
        }
        return node
      }
      let parentNode = getParentNode(event.target)
      let id = parentNode.id || ''
      let list = id.split('__')
      if (!list.length) return
      if (event.key === 'ArrowUp') {
        list[1] = +list[1] - 1 + ''
      } else if (event.key === 'ArrowDown') {
        list[1] = +list[1] + 1 + ''
      } else if (event.key === 'ArrowLeft') {
        list[2] = +list[2] - 1 + ''
      } else if (event.key === 'ArrowRight') {
        list[2] = +list[2] + 1 + ''
      }
      let turnToId = list.join('__')
      let turnToNode = document.getElementById(turnToId)
      if (turnToNode) {
        turnToNode.__vue__.focus()
      }
    },
    deleteMember (member) {
      this.$Modal.confirm({
        render: (h) => {
          return h('div', {
            domProps: {
              innerText: `确定删除成员${member.name ? member.name : ''}吗？`
            }
          })
        },
        onOk: () => this.confirmDeleteMember([member.id])
      })
    },
    async confirmDeleteMember (idList) {
      let data = await orgApi.deleteCompanyMember({ ids: idList })
      if (data) {
        this.refreshPage()
        this.$Message.success('删除成功')
      } else {
        this.$Message.error('删除失败')
      }
    },
    async setTobeManager (member, userType) {
      let params = { ids: [member.id], userType }
      let data = await orgApi.setCompanyUserType(params)
      if (data) {
        member.userType = userType
        // this.refreshPage()
        this.$Message.success('设置成功')
      } else {
        this.$Message.error('设置失败')
      }
    },
    /** **********************编辑用户信息************************/
    editMemberInfo (member) {
      this.editing.info = _.cloneDeep(member)
      this.editing.show = true
    },
    selectImage () {
      this.$refs.avatarInput.click()
    },
    uploadImage (event) {
      if (event.target.files.length) {
        const file = event.target.files[0]
        uploadFile(file).then(resp => {
          this.editing.info.avatar = resp.data
          this.$store.commit('user/setUserInfo', this.userInfo)
        })
      }
    },
    rebind (type) {
      this.editing.rebind.show = true
      this.editing.rebind.frozen = false
      this.editing.rebind.type = type
      this.editing.rebind.phone = ''
      this.editing.rebind.email = ''
      this.getNewVerifyImage()
    },
    getNewVerifyImage () {
      this.editing.rebind.verifyImage = ''
      this.editing.rebind.imageUUID = ''
      this.editing.rebind.imageCode = ''
      getCaptcha().then(res => {
        if (res.response.success) {
          this.editing.rebind.verifyImage = `data:image;base64,${res.data.image}`
          this.editing.rebind.imageUUID = res.data.uuid
          this.$refs.rebindImage.focus()
        }
      })
    },
    async getVerifyCode () {
      if (!this.editing.rebind.imageCode) {
        this.$Message.warning('请输入验证码')
        return
      }
      if (this.editing.rebind.type === 'phone' && !this.editing.rebind.phone) {
        this.$refs.rebindPhone.focus()
        this.$Message.warning('请输入正确手机号')
        return
      } else if (this.editing.rebind.type === 'email' && !this.editing.rebind.email) {
        this.$refs.rebindEmail.focus()
        this.$Message.warning('请输入正确邮箱账号')
        return
      }
      this.editing.rebind.frozen = true
      let params = {
        captcha: this.editing.reBind.imageCode,
        uuid: this.editing.reBind.imageUUID
      }
      let data = await validateCaptcha(params)
      if (!data.response.success) {
        this.$Message.error({
          content: '验证码错误，请重新输入',
          duration: 3,
          closable: true
        })
        this.$refs.rebindImage.focus()
        this.editing.rebind.frozen = false
        return
      }
      if (this.editing.rebind.type === 'phone') {
        this.getPhoneVerifyCode()
      } else {
        this.getEmailVerifyCode()
      }
    },
    getPhoneVerifyCode () {
      let data = {
        phone: this.editing.rebind.phone,
        type: 3,
        captcha: this.editing.rebind.imageCode,
        captchaUuid: this.editing.rebind.imageUUID
      }
      sendSmsCode(data).then(res => {
        if (res.response.success) {
          this.$Message.success({
            content: '验证码已发送，请查看手机短信',
            duration: 3,
            closable: true
          })
          this.$refs.rebindCode.focus()
        } else {
          this.$Message.error('验证码发送失败')
          this.editing.rebind.frozen = false
          this.$refs.rebindPhone.focus()
        }
      })
    },
    getEmailVerifyCode () {
      let data = {
        email: this.editing.rebind.email,
        type: 1,
        captcha: this.editing.rebind.imageCode,
        captchaUuid: this.editing.rebind.imageUUID
      }
      sendEmailVcode(data).then(res => {
        if (res.response.success) {
          this.$Message.success({
            content: '验证码已发送到新邮箱，请登录邮箱查看',
            duration: 3,
            closable: true
          })
          this.$refs.rebindCode.focus()
        } else {
          this.$Message.error('验证码发送失败')
          this.editing.rebind.frozen = false
          this.$refs.rebindEmail.focus()
        }
      })
    },
    async saveRebind () {
      if (!this.editing.rebind.verifyCode) {
        this.$Message.error('请输入验证码！')
        return
      }
      let data = {
        id: this.editing.info.id,
        smsVcode: this.editing.rebind.verifyCode
      }
      if (this.editing.rebind.type === 'phone') {
        data.phone = this.editing.rebind.phone
        savePhone(data).then(res => {
          if (res.response.success) {
            this.editing.info.phone = data.phone
            this.$Message.success('手机号绑定成功')
            this.editing.rebind.show = false
          } else {
            this.$Message.error(res.response.msg)
          }
        })
      } else {
        data.email = this.editing.rebind.email
        saveEmail(data).then(res => {
          if (res.response.success) {
            this.editing.info.email = data.email
            this.$Message.success('邮箱绑定成功')
            this.editing.rebind.show = false
          } else {
            this.$Message.error(res.response.msg)
          }
        })
      }
    },
    clearEditingInfo () {
      this.editing.show = false
      this.editing.info = {}
    },
    saveEditingInfo () {
      saveAccountInfo(this.editing.info).then(res => {
        if (res.response.success) {
          this.$Message.success('保存成功')
          if (this.editing.info.id === this.userInfo.id) {
            let userInfo = _.cloneDeep(this.$store.state.user.userInfo)
            for (let key in userInfo) {
              userInfo[key] = this.editing.info[key] || userInfo[key]
            }
            this.$store.commit('user/setUserInfo', userInfo)
          }
          this.refreshPage()
          this.clearEditingInfo()
        } else {
          this.$Message.error('保存失败')
        }
      })
    }
  },
  watch: {
    sortInfo: {
      deep: true,
      handler (newValue, oldValue) {
        this.sortMemberList()
      }
    }
  }
}
</script>

