<template>
  <div class="team-member-page">
    <div class="title">团队</div>
    <div class="page-content">
      <div class="team-list">
        <div class="personal-team">
          <div class="team-title" @click.stop="fold.personal=!fold.personal">
            <div class="divide-line"></div>
            <span>个人团队</span>
            <Icon class="md-fold" type="ios-arrow-back" v-if="fold.personal"></Icon>
            <Icon class="md-fold" type="ios-arrow-down" v-else></Icon>
            <div class="divide-line"></div>
          </div>
          <template v-if="!fold.personal">
            <div class="team team-add">
              <div class="team-add-icon" @click.stop="tryAddTeam(1)">
                <Icon type="md-add"></Icon>
              </div>
            </div>
            <div
              v-for="(team, key) of teamList"
              @click.stop="lookAtTeam(team.id)"
              :class="{
                team: true,
                'current-team': currentTeam.id === team.id
              }"
              v-if="!team.companyId"
              :key="key">
              <div class="team-icon">
                <Icon type="md-people"></Icon>
              </div>
              <div class="team-name">{{ team.name }}</div>
            </div>
          </template>
        </div>
        <div class="company-team">
          <div class="team-title" @click.stop="fold.company=!fold.company">
            <div class="divide-line"></div>
            <span>公司团队</span>
            <Icon class="md-fold" type="ios-arrow-back" v-if="fold.company"></Icon>
            <Icon class="md-fold" type="ios-arrow-down" v-else></Icon>
            <div class="divide-line"></div>
          </div>
          <template v-if="!fold.company">
            <div class="team team-add">
              <div class="team-add-icon" @click.stop="tryAddTeam(2)">
                <Icon type="md-add"></Icon>
              </div>
            </div>
            <div
              v-for="(team, key) of teamList"
              @click.stop="lookAtTeam(team.id)"
              :class="{
                team: true,
                'current-team': currentTeam.id === team.id
              }"
              v-if="team.companyId"
              :key="key">
              <div class="team-icon">
                <Icon type="md-people"></Icon>
              </div>
              <div class="team-name">{{ team.name }}</div>
            </div>
          </template>
        </div>
      </div>
      <div class="team-detail" v-if="this.currentTeam.id">
        <div class="detail-title">
          <div class="title-edit">
            <Icon
              class="edit-title-button"
              v-if="roleInfo.userType !== constMap.member || userInfo.userType !== constMap.member"
              type="ios-create"
              @click.stop="editTeam">
            </Icon>
            <span class="title-text">{{ currentTeam.name }}</span>
          </div>
          <div class="team-menu" v-if="roleInfo.userType !== constMap.member || userInfo.userType !== constMap.member">
            <div class="member-add">
              <Button type="success" @click.stop="addMember()">添加成员</Button>
            </div>
            <div class="team-delete">
              <Button type="default" @click.stop="tryRemoveTeam">解散团队</Button>
            </div>
          </div>
        </div>
        <div class="detail-member">
          <!-- <div
            v-if="userInfo.userType !== constMap.member"
            class="detail-item member-add">
            <div class="member-add-button" @click.stop="addMember()">
              <Icon type="md-add"></Icon>
            </div>
          </div> -->
          <div
            v-for="member in currentTeam.list"
            :class="{
              'detail-item': true,
              'member-self': userInfo.id === member.id
            }"
            :key="member.id">
            <div class="avatar">
              <img
                class="avatar-img"
                :src="member.avatar ? $avatarUrl.replace('avatarID', member.avatar) : '@/assets/logo.png'"
                alt="用户头像">
                <div class="user-name" v-if="member.name">{{ member.name }}</div>
            </div>
            <div class="user-info">
              <div class="info-item" v-if="member.userType === constMap.manager">
                <Icon type="md-person" class="info-icon"></Icon>
                <span class="info-content">管理员</span>
              </div>
            </div>
            <div
              class="menu">
              <Select
                v-if="(userInfo.userType !== constMap.member || roleInfo.userType !== constMap.member) && (userInfo.id !== member.id)"
                :transfer="true"
                v-model="member.userType"
                @on-change="changePermission(member)"
                style="width:6rem">
                <Option v-for="(option, index) in userTypeList" :value="option.code" :key="index">
                  {{option.name}}
                </Option>
              </Select>
              <Button
                v-if="userInfo.id === member.id"
                @click.stop="deleteMember(member)">
                退出
              </Button>
              <Button
                v-else-if="userInfo.userType !== constMap.member || roleInfo.userType !== constMap.member"
                @click.stop="deleteMember(member)">
                删除
              </Button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Modal
      :closable="false"
      :mask-closable="false"
      v-model="editing.show"
      @on-ok="saveTeamEditing"
      title="请输入团队名称">
      <Input v-model="editing.title" ref="editingInput" @on-enter="saveTeamEditing"></Input>
    </Modal>
    <Modal
      :closable="false"
      :mask-closable="false"
      v-model="addInfo.show"
      title="添加成员">
      <div v-for="(item, idx) of addInfo.list" :key="item.key">
        <Row type="flex" align="middle">
          <Col span="2" class="member-add-col">
            #{{idx+1}}
          </Col>
        <Col span="10" class="member-add-col">
            <Select
              filterable
              v-model="item.id">
              <Option v-for="member of readyList" :value="member.id" :key="member.id">
                <div class="option-content">
                  <img
                    class="option-avatar"
                    :src="member.avatar ? $avatarUrl.replace('avatarID', member.avatar) : '@/assets/logo.png'"
                    alt="用户头像" />
                  {{ member.name }}
                </div>
              </Option>
            </Select>
          </Col>
          <Col span="6" class="member-add-col">
            <Select v-model="item.userType">
              <Option v-for="(option, index) in userTypeList" :value="option.code" :key="index">
                  {{option.name}}
                </Option>
            </Select>
          </Col>
          <Col span="3" class="member-add-col">
            <Button type="primary" @click.stop="addMember(idx + 1)">增加</Button>
          </Col>
          <Col
            span="3"
            v-if="addInfo.list.length > 1"
            class="member-add-col">
            <Button type="default" @click.stop="deleteAddMember(idx)">删除</Button>
          </Col>
        </Row>
      </div>
      <div slot="footer">
        <Button type="text" @click.stop="clearAddInfo">取消</Button>
        <Button type="primary" @click.stop="confirmAddMember">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import orgApi from '@/api/org'
import { getUserType } from '@/api/invite'
import uuidv4 from 'uuid/v4'

export default {
  data () {
    return {
      /**
       * [{},]
       * id, name
       */
      teamList: [],
      currentTeam: {
        id: 0,
        name: '',
        list: []
      },
      turnToTeam: 0, // 刷新页面时跳转
      editing: {
        title: '',
        rangeType: 0,
        type: '',
        show: false
      },
      /**
       * 公司成员列表
       */
      memberList: [],
      userTypeList: [],
      addInfo: {
        list: [],
        show: false
      },
      constMap: {
        admin: 0,
        manager: 1,
        member: 2
      },
      fold: {
        personal: false,
        company: false
      }
    }
  },
  computed: {
    readyList () {
      let excludeList = {}
      this.addInfo.list.forEach(element => {
        excludeList[element.id] = 1
      })
      this.currentTeam.list.forEach(element => {
        excludeList[element.id] = 1
      })
      let list = this.memberList.filter(item => !excludeList[item.id])
      return list
    },
    userInfo () {
      return this.currentTeam.list.find(item => this.$store.state.user.userInfo.id === item.id) || {}
    },
    roleInfo () {
      return this.memberList.find(item => this.$store.state.user.userInfo.id === item.id) || {}
    }
  },
  mounted () {
    this.refreshPage()
  },
  methods: {
    refreshPage () {
      this.clearPage()
      this.getMemberList()
      this.getTeamList()
      this.getUserType()
    },
    clearPage () {
      this.memberList = []
      this.teamList = []
      this.currentTeam = {
        id: 0,
        name: '',
        list: []
      }
      this.userTypeList = []
    },
    async getUserType () {
      await getUserType().then(resp => {
        if (resp.response.success) {
          this.userTypeList = resp.data
        }
      })
    },
    async getMemberList () {
      let data = await orgApi.getMemberList()
      if (data) {
        this.memberList = data
      }
    },
    async getTeamList () {
      let personalTeamRequest = orgApi.getTeamList({ rangeType: 1 })
      let companyTeamRequest = orgApi.getTeamList({ rangeType: 2 })
      let personalTeam = await personalTeamRequest
      let companyTeam = await companyTeamRequest
      this.teamList = [...personalTeam, ...companyTeam]
      if (this.teamList) {
        let id = this.turnToTeam || (this.teamList.length ? this.teamList[0].id : 0)
        if (id) {
          this.lookAtTeam(id)
          this.turnToTeam = 0
        }
      }
    },
    async getTeamMemberList (id) {
      let params = {
        orgId: id
      }
      let data = await orgApi.getTeamMembers(params)
      if (data) {
        data.sort((a, b) => a.userType > b.userType)
        this.currentTeam.list = data
      }
    },
    lookAtTeam (id) {
      let data = this.teamList.find(item => item.id === id)
      if (!data && this.teamList.length) { // 这个队伍不存在，则跳回第一个
        data = this.teamList[0]
      }
      if (data) {
        this.currentTeam.id = data.id
        this.currentTeam.name = data.name
        this.currentTeam.list = []
        this.getTeamMemberList(this.currentTeam.id)
      }
    },
    showTeamEditing (type) {
      this.editing.type = type
      if (type === 'new') {
        this.editing.title = ''
      } else {
        this.editing.title = this.currentTeam.name
      }
      this.editing.show = true
      this.$nextTick(() => {
        this.$refs.editingInput.focus()
      })
    },
    async saveTeamEditing () {
      let params = {
        name: this.editing.title
      }
      if (this.editing.type === 'edit') {
        params.id = this.currentTeam.id
      } else {
        params.rangeType = this.editing.rangeType
      }
      let data = await orgApi.saveTeamEditing(params)
      if (data) {
        this.turnToTeam = data
        this.refreshPage()
        this.editing.show = false
      } else {
        this.$Message.error('操作失败：网络请求失败')
      }
    },
    tryAddTeam (rangeType) {
      this.editing.rangeType = rangeType
      this.showTeamEditing('new')
    },
    editTeam (type) {
      this.showTeamEditing('edit')
    },
    tryRemoveTeam () {
      let text = `解散团队后团队项目将会移动到公司公共区，确定解散团队${this.currentTeam.name || ''}吗?`
      this.$confirm(text, this.removeTeam)
    },
    async removeTeam () {
      let data = await orgApi.removeTeam({ ids: [this.currentTeam.id] })
      if (data) {
        this.$Message.success('删除成功')
        this.refreshPage()
      } else {
        this.$Message.error('删除失败')
      }
    },
    addMember (idx) {
      let data = {
        id: 0,
        userType: 2,
        key: uuidv4()
      }
      if (!idx) {
        idx = 0
        this.addInfo.list = []
        this.addInfo.show = true
      }
      this.addInfo.list.splice(idx, 0, data)
    },
    async confirmAddMember () {
      let list = []
      for (let info of this.addInfo.list) {
        if (info.id) {
          list.push({
            id: info.id,
            userType: info.userType
          })
        }
      }
      if (!list.length) {
        this.$Message.warning('操作失败：未填写任何有效数据')
        return
      }
      let params = {
        members: list,
        orgId: this.currentTeam.id
      }
      let data = await orgApi.addMemberToTeam(params)
      if (data) {
        this.lookAtTeam(this.currentTeam.id)
        this.clearAddInfo()
      } else {
        this.$Message.error('添加失败')
      }
    },
    deleteAddMember (idx) {
      this.addInfo.list.splice(idx, 1)
    },
    clearAddInfo () {
      this.addInfo.list = []
      this.addInfo.show = false
    },
    deleteMember (member) {
      if (member.userType !== this.constMap.member) {
        let list = this.currentTeam.list.filter(item => item.userType === this.constMap.manager)
        if (list.length <= 1) {
          let notice = '该成员是最后一名管理员，请先转让权限'
          if (this.userInfo.id === member.id) {
            notice = '您是团队最后一名管理员，请先转让权限'
          }
          this.$Message.warning(notice)
          return
        }
      }
      let text = `确定将${member.name}从团队${this.currentTeam.name}中移除吗?`
      if (this.userInfo.id === member.id) {
        text = `确定退出团队${this.currentTeam.name}吗`
      }
      this.$confirm(text, this.confirmDeleteMember, member.id, true)
    },
    async confirmDeleteMember (id, self) {
      let params = {
        ids: [id],
        orgId: this.currentTeam.id
      }
      let data = await orgApi.removeTeamMember(params)
      if (data) {
        if (self) {
          this.refreshPage()
        } else {
          this.lookAtTeam(this.currentTeam.id)
        }
        let notice = self ? '退出成功' : '删除成功'
        this.$Message.success(notice)
      } else {
        let notice = self ? '退出失败' : '删除失败'
        this.$Message.error(notice)
      }
    },
    async changePermission (member) {
      if (member.userType === this.constMap.member) {
        let list = this.currentTeam.list.filter(item => item.userType === this.constMap.manager)
        if (list.length < 1) {
          this.$Message.warning('团队里至少有一个管理员')
          this.lookAtTeam(this.currentTeam.id)
          return
        }
      }
      let params = {
        ids: [member.id],
        orgId: this.currentTeam.id,
        userType: member.userType
      }
      let data = await orgApi.setMemberPermission(params)
      if (data) {
        this.$Message.success('修改成功')
      } else {
        this.$Message.error('修改失败')
      }
      this.lookAtTeam(this.currentTeam.id)
    }
  }
}
</script>

